<template>
  <div id="home">




    <div id="particles-wrap">
    </div>


    <v-app
      id="inspire"
      dark
    >
      <v-navigation-drawer
        v-model="drawer"
        fixed
        clipped
        app
      >
        <v-layout>
          <v-flex>
            <v-card>
              <v-card-media
                src="http://pa6rasy2w.bkt.clouddn.com/HDplaroleface.gif"
                height="200px"
              ></v-card-media>

            </v-card>
          </v-flex>

        </v-layout>
        <v-layout>
          <v-flex class="text-xs-left ml-3">
            <v-btn icon large>
              <v-avatar  size="50px" tile>
                <img
                  src="https://image.eveonline.com/Corporation/98190062_128.png"
                  alt="Vuetify"
                >
              </v-avatar>

            </v-btn>
            <strong>Jorna Jan</strong>
          </v-flex>
        </v-layout>
        <v-list dense>

          <v-subheader class="mt-3 grey--text text--darken-1">SUBSCRIPTIONS</v-subheader>
          <v-list>
            <v-list-tile v-for="item in items2" :key="item.text" avatar @click="">
              <v-list-tile-avatar>
                <img :src="`https://randomuser.me/api/portraits/men/${item.picture}.jpg`" alt="">
              </v-list-tile-avatar>
              <v-list-tile-title v-text="item.text"></v-list-tile-title>
            </v-list-tile>
          </v-list>
          <v-list-tile class="mt-3" @click="">
            <v-list-tile-action>
              <v-icon color="grey darken-1">add_circle_outline</v-icon>
            </v-list-tile-action>
            <v-list-tile-title class="grey--text text--darken-1">Browse Channels</v-list-tile-title>
          </v-list-tile>
          <v-list-tile @click="">
            <v-list-tile-action>
              <v-icon color="grey darken-1">settings</v-icon>
            </v-list-tile-action>
            <v-list-tile-title class="grey--text text--darken-1">Manage Subscriptions</v-list-tile-title>
          </v-list-tile>

        </v-list>

        <v-footer
          height="auto"

        >
          <v-layout
            justify-center
            row
            wrap
          >

            <v-flex

              py-3
              text-xs-center
              white--text
              xs12
            >
              <strong>{{time}}</strong>
            </v-flex>
          </v-layout>
        </v-footer>

      </v-navigation-drawer>


      <v-toolbar
        color="red"
        dense
        fixed
        clipped-left
        app
      >

        <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
        <v-avatar>
          <img
            src="http://pa6rasy2w.bkt.clouddn.com/HDxiaomifenggif.gif"
            alt="John"
          >
        </v-avatar>


        <canvas class=
                  "background"
        ></canvas>

      </v-toolbar>
      <v-content>
        <VuexLearnMax />
      </v-content>

    </v-app>
  </div>

</template>

<script>
  import Particles from 'particlesjs'

  import VuexLearnMax from '@/components/VuexLearnMax'
  export default {
    data: () => ({
      drawer: true,
      indeterminate1: true,
      indeterminate2: true,
      time : new Date().toLocaleTimeString(),
      items: [
        { icon: 'trending_up', text: 'Most Popular' }

      ],
      items2: [
        { picture: 28, text: 'Joseph' }

      ]
    }),
    props: {
      source: String,
      indeterminate: false
    },
    components: {
      VuexLearnMax
    },
    methods: {
      getParticles() {

        Particles.
        init
        ({
          selector: '.background',
          speed: .1,
          color: '#000',

          maxParticles:
            150
          ,

// options for breakpoints
          responsive: [
            {
              breakpoint:
                268
              ,
              options: {
                maxParticles:
                  200
                ,
                color:
                  '#48F2E3'
                ,
                connectParticles:
                  false
              }
            }, {
              breakpoint:
                425
              ,
              options: {
                maxParticles:
                  100
                ,
                connectParticles:
                  true
              }
            }, {
              breakpoint:
                320
              ,
              options: {
                maxParticles:
                  0


              }
            }
          ]
        });
      }

    },
    mounted() {
      this.getParticles()
      setInterval(()=>{
        this.time = new Date().toLocaleTimeString()
      },1000)
      particlesJS("particles-wrap", {
        "particles": {
          "number": {
            "value": 80,
            "density": {
              "enable": true,
              "value_area": 800
            }
          },
          "color": {
            "value": "#ffffff"
          },
          "shape": {
            "type": "circle",
            "stroke": {
              "width": 0,
              "color": "#fff"
            },
            "polygon": {
              "nb_sides": 5
            },
            "image": {
              "src": "img/github.svg",
              "width": 100,
              "height": 100
            }
          },
          "opacity": {
            "value": 0.5,
            "random": false,
            "anim": {
              "enable": false,
              "speed": 1,
              "opacity_min": 0.1,
              "sync": false
            }
          },
          "size": {
            "value": 2,
            "random": true,
            "anim": {
              "enable": false,
              "speed": 40,
              "size_min": 0.1,
              "sync": false
            }
          },
          "line_linked": {
            "enable": true,
            "distance": 150,
            "color": "#ffffff",
            "opacity": 0.4,
            "width": 1
          },
          "move": {
            "enable": true,
            "speed": 2,
            "direction": "none",
            "random": false,
            "straight": false,
            "out_mode": "out",
            "bounce": false,
            "attract": {
              "enable": false,
              "rotateX": 600,
              "rotateY": 1200
            }
          }
        },
        "interactivity": {
          "detect_on": "canvas",
          "events": {
            "onhover": {
              "enable": true,
              "mode": "grab"
            },
            "onclick": {
              "enable": true,
              "mode": "push"
            },
            "resize": true
          },
          "modes": {
            "grab": {
              "distance": 150,
              "line_linked": {
                "opacity": 1
              }
            },
            "bubble": {
              "distance": 400,
              "size": 40,
              "duration": 2,
              "opacity": 8,
              "speed": 3
            },
            "repulse": {
              "distance": 200,
              "duration": 0.4
            },
            "push": {
              "particles_nb": 4
            },
            "remove": {
              "particles_nb": 2
            }
          }
        },
        "retina_detect": true
      });


    }
  }
</script>
<style scoped>
  html, body {
    margin: 0;
    padding: 0;
  }
  .background {
    position: absolute;
    display: block;
    top: 0;
    left: 0;
    z-index: -1;
  }
  #particles-wrap{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

  }
  #home {
    position: relative;
  }
  .progress{
    position: absolute;
    top: 0;
    left: 0;
    z-index: 100;
  }
</style>



