import Vue from 'vue'
import Router from 'vue-router'
import VuexLearnMax from '@/components/VuexLearnMax'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'VuexLearnMax',
      component: VuexLearnMax
    }
  ],
  scrollBehavior: function (to, from, savedPosition) {
    return savedPosition || { x: 0, y: 700 }
  }

})




