<template>
  <v-container fluid>

    <div id="particles-js"></div>



    <v-slide-y-transition mode="out-in">
      <v-layout column align-center>
        <v-container grid-list-md text-xs-center>
          <v-layout row wrap>

            <v-flex  xs6>

              <v-card>
                <v-toolbar color="teal" dark>
                  <v-toolbar-side-icon></v-toolbar-side-icon>

                  <v-toolbar-title>要添加的对象</v-toolbar-title>
                </v-toolbar>

                <v-divider></v-divider>

                <v-list
                  subheader
                  two-line
                >

                  <v-list-tile @click=""  v-for="(user, index) in unregisteredUsers"
                               :key="index">

                    <v-list-tile-content
                                        >
                      <v-list-tile-title>{{user.name}}</v-list-tile-title>
                      <v-list-tile-sub-title>{{user.introduce}}</v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <div>
                        <v-btn small color="info" @click="register(user)">
                          <v-progress-circular
                            indeterminate
                            color="primary"
                            v-show="isLoding"
                          ></v-progress-circular>
                          添加</v-btn>
                      </div>
                    </v-list-tile-action>

                  </v-list-tile>


                </v-list>
              </v-card>

            </v-flex>
            <v-flex  xs6>

              <v-card>
                <v-toolbar color="teal" dark>
                  <v-toolbar-side-icon></v-toolbar-side-icon>

                  <v-toolbar-title>添加进来的对象</v-toolbar-title>
                </v-toolbar>

                <v-divider></v-divider>

                <v-list
                  subheader
                  two-line
                >
                  <v-btn

                    color="indigo"
                    class="white--text"
                    fab
                    icon
                    small
                    v-show="registeration.length!=0"
                  >
                    {{totalRegistrations}}
                  </v-btn>
                  <v-list-tile v-show="registeration.length==0" >
                    <v-list-tile-content>
                      <v-list-tile-title class="text-xs-center">
                        暂无数据
                      </v-list-tile-title>



                    </v-list-tile-content>


                  </v-list-tile>
                  <v-list-tile @click=""
                      v-for="(user, index) in registeration" :key="index">

                    <v-list-tile-content>
                      <v-list-tile-title>{{user.name}}

                      </v-list-tile-title>


                      <v-list-tile-sub-title>{{user.introduce}}</v-list-tile-sub-title>
                    </v-list-tile-content>
                    <v-list-tile-action>
                      <div>
                        <v-chip small color="red" text-color="white">{{user.date}}</v-chip>


                        <v-btn color="info" small @click="unregister(user)">取消</v-btn>
                      </div>

                    </v-list-tile-action>

                  </v-list-tile>


                </v-list>
              </v-card>

            </v-flex>
          </v-layout>
        </v-container>
      </v-layout>

    </v-slide-y-transition>
    <v-layout row wrap>
      <v-flex md6 lg6>
        <v-time-picker
          v-model="time"
          :allowed-hours="allowedHours"
          :allowed-minutes="allowedMinutes"
          class="mt-3"
          format="24hr"
          scrollable
          min="9:30"
          max="22:15"
        ></v-time-picker>
      </v-flex>
      <v-flex md5>
        <code>npm install</code>
      </v-flex>
    </v-layout>

  </v-container>
</template>

<script>

  import { mapGetters, mapMutations, mapActions } from 'vuex'
  export default {
    data() {
      return {
        time: '11:55',
        timeStep: '10:10'

      }
    },
    computed: {
      ...mapGetters(['unregisteredUsers','registeration', 'totalRegistrations', 'isLoding'])
    },
    methods: {
      ...mapMutations(['unregister']),
      ...mapActions(['register']),
      allowedHours: v => v % 2,
      allowedMinutes: v => v >= 10 && v <= 50,
      allowedStep: (m) => m % 10 === 0,

    },
    mounted(){
      particlesJS("particles-js", {
          "particles": {
            "number": {
              "value": 100
            },
            "color": {
              "value": "#fff"
            },
            "shape": {
              "type": "circle",
              "stroke": {
                "width": 1,
                "color": "ccc"
              }
            },
            "opacity": {
              "value": 0.5,
              "random": true,
              "anim": {
                "enable": true,
                "speed": 1
              }
            },
            "size": {
              "value": 5,
              "random": true,
              "anim": {
                "enable": true,
                "speed": 30
              }
            },
            "line_linked": {
              "enable": true,
              "distance": 120,
              "color": "#fff",
              "width": 1
            },
            "move": {
              "enable": true,
              "speed": 2,
              "direction": "bottom-right",
              "straight": false
            }
          },
          "interactivity": {
            "events": {
              "onhover": {
                "enable": true,
                "mode": "repulse"
              },
              "onclick": {
                "enable": true,
                "mode": "remove"
              }
            },
            "modes": {
              "repulse": {
                "distance": 50,
                "duration": 0.4
              },
              "bubble": {
                "distance": 100,
                "size": 10
              }
            }
          }
        }
      );


    }
  }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#particles-js{
  background: transparent;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
</style>



